import { v1_domains_availability } from "src/redux/actions/api/v1"
// import * as io_actions from "src/redux/actions/io"
// const sleep = function (ms = 0) {
//   return new Promise((r) => setTimeout(r, ms))
// }

export function RX__domains_availability(availability_dict) {
  return async (dispatch, getState) => {
    // console.warn(performance.now(), " received RX__domains_availability")
    let { input, output } = getState()
    let { domains_availability, domains_suggested = {} } = output
    /*
     * Make flat list of domain names to POST
     */
    let suggestions = []
    if (availability_dict) {
      suggestions = availability_dict
    } else {
      // loop each group, then add each item inside to flat suggestions list:
      for (let type in domains_suggested) {
        let list = domains_suggested[type]
        if (list && list.length) {
          // loop each item, to add:
          for (let item of list) {
            suggestions.push(item)
          }
        }
      }
    }
    /*
     * Fix domains
     */
    // remove spaces
    suggestions = suggestions.map((word) => word.replace(/ /g, ""))
    // keep only ones which don't already have availability score
    suggestions = suggestions.filter((word) => {
      return !domains_availability[word] || domains_availability[word] == 0 || domains_availability[word] == -1
    })
    /*
     * Fetch availability of each suggestion string
     * (50 at a time)
     */
    if (suggestions.length) {
      // let suggestions_slice = suggestions
      // suggestions = suggestions.slice(0,100) // debug limited list
      let i_page = 0
      let n_page = Math.ceil(Math.max(28, suggestions.length / 3)) // browser allows 6 concurrent connections
      while (i_page * n_page < suggestions.length) {
        let start = i_page * n_page
        let end = i_page * n_page + n_page
        let suggestions_slice = suggestions.slice(start, end)
        if (suggestions_slice.length) {
          let params = {
            options: {},
            domains: suggestions_slice,
            recaptcha2_token: input.captcha2,
            recaptcha3_token: input.captcha3
          }
          // console.warn("send                        v1_domains_availability")
          // console.timeEnd("/ds")
          // console.time("/ds")
          console.warn("sending v1_domains_availability( alt= true )")
          params.options.use_alt_source = i_page === 0 ? true :  false
          v1_domains_availability(params).then((data = {}) => {
            if (data.availability) {
              console.warn("received v1_domains_availability( alt= true )", data)
              // save
              dispatch({
                type: "RX__domains_availability",
                data: data.availability
              })
              /*
               * IF USE_CLI, AND NOT FOUND, TRY AGAIN
               * run same API endpoint again, but this time without CLI PING
               */
              // let try_again_arr = []
              // // look at all INPUT dict
              // for (let domname of suggestions_slice) {
              //   // see if it exists in the OUTPUT dict
              //   if (
              //     !data.availability[domname] ||
              //     (data.availability[domname].length && !data.availability[domname][0])
              //   ) {
              //     // missing! try again!
              //     try_again_arr.push(domname)
              //   }
              // }
              // if (try_again_arr.length) {
              //   params.domains = try_again_arr // check only the missing ones
              // }
            }
          })
          setTimeout(() => {
            console.warn("sending v1_domains_availability( alt= false )")
            params.options.use_alt_source = i_page === 0 ? false : true
            v1_domains_availability(params).then((data) => {
              if (data && typeof data === "object" && "availability" in data) {
                console.warn("received v1_domains_availability( alt= false )", data)
                // save
                dispatch({
                  type: "RX__domains_availability",
                  data: data.availability
                })
              }
            })
          }, 1000)
        }
        i_page++
      }
    }
  }
}
